.gpt3__features-container_feature{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: flex-start;
    margin: 1rem;
    width: 100%;
}
.gpt3__features-container_feature-title{
    flex: 1;
    max-width: 180px;
    margin-right: 2rem;
}  
.gpt3__features-container_feature-title h1{
   font-family: var(--font-family);
   font-weight: 800;
   font-size: 18px;
   line-height: 24px;
   color: #fff;
   letter-spacing: -0.04em;
}
.gpt3__features-container_feature-title div{
     width: 38px;
     height: 3px;
     box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
     margin-bottom: 0.25rem;
     background: var(--gradient-bar);
}
.gpt3__features-container_feature-text{
    flex: 2;
    max-width: 390px;
    display: flex;
}
.gpt3__features-container_feature-text p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-text);
}
@media screen and (max-width:550px) {
    .gpt3__feature-container_feature-title h1{
        font-size: 14px;
        line-height: 22px;
    }
    .gpt3__feature-container_feature-title p{
        font-size: 12px;
        line-height: 20px;
    }
    .gpt3__feature-container_feature{
        margin: 1rem 0;
    }
}